body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-attachment: fixed;
    background-position: 50%;
    background-size: cover;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

#content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
}

header {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
    background-color: black;
    opacity: 80%;
    box-sizing: border-box;
    padding-bottom: 10px;
    font-size: 2rem;
}

nav {
    display: flex;
    gap: 50px;
}

nav>button {
    height: 40px;
    width: 100px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 1.5rem;
    font-weight: 900;
    color: white;
    background-color: darkgreen;
    border-radius: 10px;
}

.clickable:hover,
.clickable:focus {
    cursor: pointer;
    transition: all .2s ease-in-out;
    transform: scale(1.1);
}

main {
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    padding: 50px;
}

#display {
    min-height: 500px;
    width: 800px;
    background-color: black;
    opacity: 70%;
    padding: 0 50px;
    box-sizing: border-box;
    color: white;
}

/* home tab */

.home>h1 {
    text-align: center;
    font-size: 3rem;
    margin: 15px 0;
}

.home>p {
    font-size: 2rem;
    font-weight: 600;
    margin: 10px 0;
}

.home>.note {
    font-size: 1rem;
    text-decoration: underline;
    margin-top: 40px;
}

/* menu tab */

#display.menu {
    opacity: 80%;
}

.menu>h1 {
    text-align: center;
    font-size: 3rem;
    font-weight: 600;
}

.menu>.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    box-sizing: border-box;
    padding-bottom: 50px;
    gap: 50px;
}

.menu>.container>.dish {
    height: 360px;
    width: 250px;
    text-align: center;
    padding: 5px;
    background-color: white;
    color: black;
    font-size: 1.1rem;
    font-weight: 600;
    position: sticky;
    opacity: 100%;
}

.menu>.container>.dish>img {
    height: 200px;
    width: 250px;
}

/* contact tab */

#display.contact {
    opacity: 90%;
}

.contact>h1 {
    text-align: center;
    font-size: 3rem;
}

.contact>.container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    padding: 15px;
    gap: 30px;
}

.contact>.container>.info {
    display: flex;
    flex-direction: column;
    font-size: 1.1rem;
}

footer {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    opacity: 70%;
    color: white;
    font-size: 1.5rem;
    font-weight: 900;
    height: 50px;
}

footer>p>a {
    color: green;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;IAEI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,yDAAwD;IACxD,4BAA4B;IAC5B,wBAAwB;IACxB,sBAAsB;IACtB,sEAAsE;AAC1E;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,8BAA8B;IAC9B,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,sBAAsB;IACtB,oBAAoB;IACpB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,sEAAsE;IACtE,iBAAiB;IACjB,gBAAgB;IAChB,YAAY;IACZ,2BAA2B;IAC3B,mBAAmB;AACvB;;AAEA;;IAEI,eAAe;IACf,+BAA+B;IAC/B,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,eAAe;IACf,sBAAsB;IACtB,YAAY;AAChB;;AAEA,aAAa;;AAEb;IACI,kBAAkB;IAClB,eAAe;IACf,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,0BAA0B;IAC1B,gBAAgB;AACpB;;AAEA,aAAa;;AAEb;IACI,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,uBAAuB;IACvB,sBAAsB;IACtB,oBAAoB;IACpB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,iBAAiB;IACjB,gBAAgB;IAChB,gBAAgB;IAChB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,YAAY;AAChB;;AAEA,gBAAgB;;AAEhB;IACI,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,mBAAmB;IACnB,uBAAuB;IACvB,sBAAsB;IACtB,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,uBAAuB;IACvB,YAAY;IACZ,YAAY;IACZ,iBAAiB;IACjB,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB","sourcesContent":["html,\nbody {\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nbody {\n    background-image: url(/src/images/forest_restaurant.jpg);\n    background-attachment: fixed;\n    background-position: 50%;\n    background-size: cover;\n    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;\n}\n\n#content {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    height: 